import './App.css';
import React, { useState } from 'react'

// import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
// import {
//   BrowserRouter as Router,
//   Switch,
//   Route,
// } from "react-router-dom";


function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type)=>{
    setAlert({
      msg: message,
      type : type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }


  const toggleMode = () =>{
      if (mode === 'light') {
        setMode('dark')
        document.body.style.backgroundColor='#042743';
        showAlert("DarkMode is Enabled!!","success");
        document.title ='TextUtlis-DarkMode';
      // setInterval(() => {
      //   document.title ='TextUtlis is Running';
      // }, 500);
      // setInterval(() => {
      //   document.title ='Install TextUtlis now';
      // }, 1000);
    }
      else{
        setMode('light')
        document.body.style.backgroundColor='white';
        showAlert("LightMode is Enabled!!","success")
        document.title ='TextUtlis-LightMode';
        
      }
  }
  const changeBg = () =>{
    if (mode === 'light') {
      setMode('dark')
      document.body.style.backgroundColor='#8B0000';
      showAlert("DarkMode is Enabled!!","success")
      
    }
    else{
      setMode('light')
      document.body.style.backgroundColor='white';
      showAlert("LightMode is Enabled!!","success")
      
    }
}
    
  return (
    <>
    {/* <Router> */}
        {/* <Navbar title = "TextUtils" about= "About TextUtils"/> */}
         <Navbar title = "TextUtils"  mode={mode} toggleMode = {toggleMode} changeBg = {changeBg} />
       <Alert alert={alert}/>
      <div className="container my-3">
       {/* <Switch>
         <Route exact path="/about">
           <About />
         </Route> */}

         {/* <Route exact path="/"> */}
            <TextForm  showAlert ={showAlert} heading="Enter text to analyze" mode={mode}/>
          {/* </Route> */}
        {/* </Switch> */}
      </div>
    {/* </Router> */}
    </>
  );
}

export default App;
