import React, { useState } from 'react'


function TextForm(props) {
  const handleUpClick = ()=>{
    // console.log('Clicked');
    let newText = text.toUpperCase();
    setText(newText)
    props.showAlert('Converted to UpperCase', 'success');
  }
  const handleLoClick = ()=>{
    // console.log('Clicked');
    let newText = text.toLowerCase();
    setText(newText)
    props.showAlert('Converted to LowerCase', 'success');

  } 
  const handleClear = ()=>{
    // console.log('Clicked');
    let newText = '';
    setText(newText)
    props.showAlert('Playground is Clear', 'success');

  }
   const handleCopy = ()=>{
    // console.log('Clicked');
    let text = document.getElementById('myBox');
    text.select();
    text.setSelectionRange(0,9999);
    navigator.clipboard.writeText(text.value)
    props.showAlert('Copied to Clipboard', 'success');

  } 
  const handleCapClick = ()=>{
    // console.log('Clicked');
    let newText = text.charAt(0).toUpperCase()+text.slice(1);
    setText(newText)
  }
  const handleOnChange = (e)=>{
    // console.log('On Change');
    setText(e.target.value);
  }
  const [text, setText] = useState('');
  return (
    <>
    <div className = "container" style={{color : props.mode === 'light'?'#042743':'white'}}>
      <h1>{props.heading}</h1>
      <div className="mb-3">
        <textarea className="form-control" value={text} onChange={handleOnChange} id="myBox" rows="8" style={{backgroundColor : props.mode === 'light'?'white':'grey', color : props.mode === 'light'?'#042743':'white'}}></textarea>
      </div>
      <button className="btn btn-primary mx-3" onClick={handleUpClick}>Convert to UpperCase</button>
      <button className="btn btn-danger mx-3" onClick={handleLoClick}>Convert to LowerCase</button>
      <button className="btn btn-success mx-3" onClick={handleCapClick}>Convert initial letter to UpperCase</button>
      <button className="btn btn-primary mx-3" onClick={handleClear}>Clear Text</button>
      <button className="btn btn-primary mx-3" onClick={handleCopy}>Copy Text</button>
    </div>
    <div className="container my-3" style={{color : props.mode === 'dark'?'white':'#042743'}}>
      <h3>Your Text Summary</h3>
      <p>{text.length>0?text.trim().split(" ").length :0}   words and {text.length} characters are present.</p>
      <p>{0.008*text.split(" ").length} Minutes to read</p>
      <h3>Preview</h3>
      <p>{text.length>0 ?text:'Enter text above to get preview'}</p>
    </div>
    </>
  )
}

export default TextForm
